@font-face {
  font-family: Humanst521;
  src: url(fonts/humanist521bt_roman.ttf);
}

  .App {
    text-align: center;
    font-family: Humanst521;
    overflow-x: hidden;
  }
  p, input, textarea, button {
    font-family: Humanst521;
  }
  img {
    -webkit-user-drag: none;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
  }
  .img-hor-vert {
    -moz-transform: scale(-1, 1);
    -o-transform: scale(-1, 1);
    -webkit-transform: scale(-1, 1);
    transform: scale(-1, 1);
}
  #logo {
    width: 90vw;
    margin: 5rem auto;
    overflow-x: hidden;
  }
  .circles {
    width: 50vh;
    margin-top: 25vh;
  }
  p, h2, h1 {
    cursor: default;
  }
  h1 {
    font-size: calc(1.375rem + 1.5vw);
    margin: 0.5rem 15px;
    color: #323E67;
  }
  h2 {
    font-size: calc((1.325rem + 0.9vw)/2);
    color: #323E67;
    opacity: 0.5;
  }
  #motto span:first-child {
    background: linear-gradient(90deg, #3795F2 24.68%, #052A5E 108.23%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  #motto span:last-child {
    background: linear-gradient(90deg, #3795F2 24.68%, #021E48  108.23%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  #firstFeature {
    display: flex;
    justify-content: space-around;
  }
  #firstFeature svg {
    width: 15vw;
    fill: #465fb1;
  }

  #smallMotto1 {
    margin-bottom: 10rem;
  }

  #firstFeatureClouds {
    margin-top: 18rem;
    height: 25vh;
    fill: #2da35e;
    display: flex;
    justify-content: center;
  }
  #firstFeatureClouds svg {
    margin:  auto 0;
    height: 13vw;
    width: auto;
    fill: #2da35e;
  }
  #firstFeatureClouds svg:first-child {
    margin-right: 5rem;
  }
  #firstFeatureClouds svg:last-child {
    margin-left: 5rem;
  }

  #cloudIcons {
    margin-top: 17.5rem;
    display: flex;
    justify-content: center;
    height: 25vh;
  }
  #cloudIcons svg {
    margin: 0;
    width: 5vw;
    fill: #2da35e;
  }
  #cloudIcons svg:first-child {
    margin-right: calc(5rem + 4vw);
  }
  #cloudIcons svg:last-child {
    margin-left: calc(5rem + 4vw);
  }

  

  #secondFeature {
    display: flex;
    justify-content: center;
  }
  #secondFeature svg {
    fill: #465fb1;
    margin: 5rem;
    width: 200px;
  }

  #thirdFeature svg{
    width: 60vw;
  }
  .basic-grid {
    display: grid;
    gap: 1rem;
    width: 80vw;
    margin: auto auto;
    grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  }
  .card-tall {
    grid-row: span 2 / auto;
  }
  .card-wide {
    grid-column: span 2 / auto;
  }
  .card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #e7e7e7;
    color: #fff;
    box-shadow: rgba(3, 8, 20, 0.1) 0px 0.15rem 0.5rem, rgba(2, 8, 20, 0.1) 0px 0.075rem 0.175rem;
    height: 100%;
    width: 100%;
    border-radius: 4px;
    transition: all 500ms;
    overflow: hidden;
 }
.card:hover {
    box-shadow: rgba(2, 8, 20, 0.1) 0px 0.35em 1.175em, rgba(2, 8, 20, 0.08) 0px 0.175em 0.5em;
    transform: translateY(-3px) scale(1.05);
}
.card img {
  transition: all 500ms;
  position: center;
  object-fit: cover;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
}
.card:hover img {
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    filter: blur(5px);
 }
 .card p {
    visibility: hidden;
    opacity: 0;
    transition: all 500ms;
    width: 90%;
    text-align: center;
    text-shadow: 1px 1px 3px #353535;

    position: absolute;
    top: 50;
    left: 50;
 }
 .card:hover p {
    visibility: visible;
    opacity: 1;
  }

  .form-elements {
    background-color: #b3e8ff;
    max-width: 80vw;
    margin: 50px auto;
    padding: 10px;
    border-radius: 50px;
  }
  .form-elements button {
    border: none;
    border-radius: 50px;
    padding: 20px;
    min-width: 50%;
    font-size: calc((1.325rem + 0.9vw)/1.5);
    background-color: #465fb1;
    color: white;
    margin: 10px;
  }
  .form-elements button:hover {
    cursor: pointer;
  }

  .pure-group {
    border: none;
  }
  .pure-group input {
    border: none;
    background-color: #efefef;
    border-radius: 30px;
    padding: 20px;
    width: 80%;
    margin: 0;
    font-size: calc((1.325rem + 0.9vw)/1.5);
  }
  .pure-group textarea {
    border: none;
    background-color: #efefef;
    border-radius: 30px;
    padding: 20px;
    width: 80%;
    margin: 0;
    font-size: calc((1.325rem + 0.9vw)/1.5);
    resize: none;
    min-height: 2rem;
  }
  .pure-group input:focus, .pure-group textarea:focus {
    outline: none;
    border:none;
  }

  .honeypot-field {
    visibility: hidden;
  }
